<template>
    <!-- 订单确认 -->
    <div>
        <van-cell title="地址" value=">" @click="go" />
        <van-card
        v-for="item in list" :key="item.id"
        :num="item.num"
        :price="item.pri"
        desc="优质水果"
        :title="item.name"
        :thumb="item.src"
        />
        <van-submit-bar
        :disabled="!list.length"
        :price="totalNum"
        button-text="提交订单"
        tip="你的收货地址不支持同城送, 我们已为你推荐快递"
        tip-icon="info-o"
        @submit="onSubmit"
        />
    </div>
</template>

<script>
import {mapState, mapGetters} from "vuex"
import { Notify } from 'vant';
export default {
    computed: {
        ...mapState("shoppingCar", ["list"]),
        ...mapGetters("shoppingCar", ["total"]),
        totalNum() {
            return +(parseInt(this.total)+"00");
        }
    },
    methods: {
        onSubmit() {
            Notify({ type: 'danger', message: '提交成功',
            duration: 1000, });
            setTimeout(() => {
                this.$store.commit("shoppingCar/clear")
            }, 1000);
        },
        go() {
            this.$router.push({
                name: "address"
            })
        }
    }
}
</script>

